.menu{
    padding: 0.5rem;
}
.menu__panel{
 padding: 0.5rem;
}

.menu__title{
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
    background-color: $color-normal;
    font-family: 'Open Sans';
    color: $color-white;
    padding: 0.5rem;
    font-size: 1.5rem;
    text-align: center;
    
}
.menu__list{
    border-bottom-left-radius: 0.5rem;
    border-bottom-right-radius: 0.5rem;
    background-color: $color-dark;
    font-family: 'Open Sans';
    color: $color-white;
    margin-bottom: 1rem;
    text-align: center;
    height: 64%;
    a{
        color: $color-white;
        text-decoration: none;
        padding: 0.5rem 1rem;
        display: block;
    

        &:hover{
            background-color:#95A5A6 ;
            color: $color-white;
        }
    }
    }